import Vue from 'vue'
import VueRouter from 'vue-router'
import NotFound from '@/views/NotFound.vue'
import Home from '@/views/Home.vue'
import Cart from '@/views/Cart.vue'
import Orders from '@/views/Orders.vue'

import Product from '@/views/Product.vue'
import Model from '@/components/Model.vue'
import Parts from '@/components/Parts.vue'
import Book from '@/components/Book.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'NotFound',
    components: NotFound
  },
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  {
    path: '/orders',
    name: 'Orders',
    component: Orders
  },
  {
    path: '/product/:group',
    component: Product,
    children: [
      {
        path: '',
        name: 'Model',
        component: Model
      }
    ]
  },
  {
    path: '/product/:group/:model',
    component: Product,
    children: [
      {
        path: '',
        name: 'Parts',
        component: Parts
      }
    ]
  },
  {
    path: '/product/:group/:model/:parts',
    component: Product,
    children: [
      {
        path: '',
        name: 'Book',
        component: Book
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
