<template>
  <section>
    <div class="container">
      <h2 class="title">{{this.partsItem[0].title}}</h2>

      <ul class="tab-group">
        <li
          v-for="(item, index) in tabGroup"
          :key="index"
          class="tab-list"
          v-bind:class="[index==selected ? 'active' : '']"
          v-on:click="fn_tab(index)"
        >{{ item }}</li>
      </ul>
      <div class="tab-contents">
        <div class="tab-contents-list drawing" v-bind:class="[0==selected ? 'active' : '']">
          <img class="list-drawing" v-bind:src="this.partsItem[0].image" v-if="this.partsItem[0].image" />
        </div>
        <div class="tab-contents-list designation" v-bind:class="[1==selected ? 'active' : '']">
          <table>
            <thead>
              <tr>
                <td>no</td>
                <td>title</td>
                <td>contents</td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>1</td>
                <td>aaaaaa</td>
                <td>what are you want</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      selected: 0,
      group: this.$route.params.group,
      model: this.$route.params.model,
      parts: this.$route.params.parts,
      partsItem: null,
      tabGroup: ["Drawing", "Designation"],
      items: [
        {
          idx: 0,
          groupIdx: 0,
          title: "Input Shaft Group",
          image: require('@/assets/images/025STDCA100.png'),
          revision: true
        },
        {
          idx: 1,
          groupIdx: 0,
          title: "Asterm Shaft Group",
          image: require('@/assets/images/025STDAS100.png'),
          revision: false
        },
        {
          idx: 2,
          groupIdx: 1,
          title: "Output Shaft Group",
          image: ""
        },
        {
          idx: 3,
          groupIdx: 1,
          title: "Pump holder Group",
          image: ""
        },
        {
          idx: 4,
          groupIdx: 1,
          title: "Valve holder Group",
          image: ""
        },
        {
          idx: 5,
          groupIdx: 2,
          title: "Case Group",
          image: ""
        }
      ]
    };
  },
  created() {
    this.$store.commit("oderStep", 2);
    this.partsItem = this.fn_item(this.parts);
  },
  methods: {
    fn_item: function(_value) {
      return this.items.filter(function(item) {
        return item.idx + "" === _value + "";
      });
    },
    fn_tab: function(_value) {
      this.selected = _value;
    }
  }
};
</script>

